<template>
  <div>
    <div class="has-background-black">
      <div style="max-width:1280px;max-height:720px;background-color:black" class="mx-auto">
        <div class="image is-16by9">
          <video id="player" autoplay poster="http://localhost:8080/img/1.c7038f01.jpg" class="has-ratio" :src="'https://159.65.224.65:443/beat/now_'+dj.token+'/manifest.mpd'" data-viblast-key="56c06dca-ea63-4117-93fa-667f801534b7" width="1280"></video>
        </div>
      </div>
    </div>

    <div class="media">
      <div class="media-left pl-3">
        <p class="image">
          <img class="is-rounded" style="max-height: 64px;max-width: 64px;"  v-if="dj.pictures.length" :src="dj.pictures[0]">
          <b-skeleton circle height="64px" width="64px" v-else :animated="false"></b-skeleton>
        </p>
      </div>
      <div class="media-content is-clipped">
        <p class="title is-4">{{dj.info.subtitle}}</p>
        <p class="subtitle is-5">
          {{dj.info.title}}
          <span v-if="iFollow && me" class="ml-2"><a @click="follow"><b-icon icon="heart"></b-icon></a></span>
          <span v-else-if="me" class="ml-2"><a @click="follow"><b-icon icon="heart-outline"></b-icon></a> </span>
        </p>
      </div>
      <div class="media-right pr-3">
        <div class="navbar-item" v-if="!me">
            <div class="buttons">
            <a class="button is-light outlined" @click="openLogin(0)">
                Log in
            </a>
            <a class="button is-primary" @click="openLogin(1)">
                <strong>Sign up</strong>
            </a>
            
            </div>
        </div>
      </div>
    </div>
    <div class="mx-5 mt-5">
      <div class="columns">
        <div class="column is-one-quarter">
          <div class="box">
            <p class="is-6 mb-4">{{dj.info.title}}</p>
            <p>
              {{dj.info.description}}
            </p><br>
            Member since {{memberSince}}
            <a class="button" @click="testPlayer">
                Test
            </a>
          </div>
            <div class="box">
              <p class="is-6 mb-4">Social</p>
            <span class="mr-2" v-if="dj.info.social.facebook"><a :href="dj.info.social.facebook" target="_blank"><b-icon type="is-facebook" icon="facebook"></b-icon></a></span>
            <span class="mr-2" v-if="dj.info.social.instagram"><a :href="dj.info.social.instagram" target="_blank"><b-icon type="is-instagram" icon="instagram"></b-icon></a></span>
            <span class="mr-2" v-if="dj.info.social.spotify"><a :href="dj.info.social.spotify" target="_blank"><b-icon type="is-spotify" icon="spotify"></b-icon></a></span>
            <span class="mr-2" v-if="dj.info.social.soundcloud"><a :href="dj.info.social.soundcloud" target="_blank"><b-icon type="is-soundcloud" icon="soundcloud"></b-icon></a></span>
            <span v-if="dj.info.social.twitter"><a :href="dj.info.social.twitter" target="_blank"><b-icon type="is-twitter" icon="twitter"></b-icon></a></span>
        </div>  
          </div>
        <div class="column">
          <b-tabs  v-model="activeTab" class="block">
            <b-tab-item >
                <template slot="header">
                    <span> Biography </span>
                </template>
                <span v-html="dj.info.bio"></span>
            </b-tab-item>
            <b-tab-item disabled>
                <template slot="header">
                    <span> Latest Videos <b-tag > Comming Soon </b-tag> </span>
                </template>
            </b-tab-item>
          </b-tabs>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import _ from 'lodash'
import LoginModal from '@/components/LoginModal' 

export default {
  data(){
    return {
      activeTab:0
    }
  },
  methods:{
    follow: _.debounce(function(){
      if(this.iFollow){
        this.me.follows=this.me.follows.filter(val=>{
          return val != this.dj._id
        })       
      } else{
        this.me.follows.push(this.dj._id)
      }
      this.editMe({
          follows:this.me.follows
      })
    },1000),
    openLogin(section){
            this.$buefy.modal.open({
                parent: this,
                component: LoginModal,
                hasModalCard: true,
                props:{
                    activeTab:section,
                },
                canCancel:['escape', 'outside'],
                customClass: '',
                trapFocus: true
            })
    },
    testPlayer(){
      var player = document.getElementById('player')
      player.requestFullscreen()
      var api = player.viblast
      console.log(api)
      
    },
    ...mapActions([
      'editMe'
    ])
  },
  computed:{
    ...mapState([
      'dj',
      'me'
    ]),
    iFollow(){
      if(this.me){
        return this.me.follows.includes(this.dj._id)
      }else{
        return true
      }
    },
    memberSince(){
      const d = new Date(this.dj.createdAt)
      const ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(d)
      const mo = new Intl.DateTimeFormat('en', { month: 'short' }).format(d)
      const da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(d)
      return `${da}-${mo}-${ye}`
    }
  },
  mounted(){
  }
}
</script>
